    display: flex;
    flex-direction: column;
  }
}
.main-navigation {
  display: flex;
  flex-direction: column;
}
.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 32px;
  right: 31px;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
.main-navigation--closed .main-navigation__toggle:hover::before {
  background-color: rgba(0, 0, 0, 0.6);
  box-shadow: 0 6px 0 0 rgba(0, 0, 0, 0.6), 0 12px 0 0 rgba(0, 0, 0, 0.6);
}
.main-navigation--closed .main-navigation__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 6px 0 0 rgba(0, 0, 0, 0.3), 0 12px 0 0 rgba(0, 0, 0, 0.3);
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 38px;
  right: 28px;
  width: 28px;
  height: 2px;
  background-color: #000000;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation--opened:hover::before {
  background-color: rgba(0, 0, 0, 0.6);
}
.main-navigation--opened:hover::after {
  background-color: rgba(0, 0, 0, 0.6);
}
.main-navigation--opened:active::before {
  background-color: rgba(0, 0, 0, 0.6);
}
.main-navigation--opened:active::after {
  background-color: rgba(0, 0, 0, 0.6);
}
.main-navigation__toggle {
  background-color: #ffffff;
  position: absolute;
  top: 0;
  right: 0;
  min-height: 79px;
  width: 82px;
  align-self: flex-end;
  border: none;
  cursor: pointer;
}
.main-navigation__wrapper {
  position: absolute;
  z-index: 2;
  width: 100%;
  background-color: #ffffff;
}
.main-navigation__list {
  margin: 0;
  padding: 0;
