    background-position: -1px 47px;
  }
  .video__title {
    padding: 0;
    margin: 0;
    padding-top: 96px;
    max-width: 178px;
  }
  .video__preview-container {
    grid-row: 1 / 5;
    position: relative;
  }
  .video__preview-icon {
    position: absolute;
  }
  .video__preview-icon:hover {
    transform: scale(120%);
  }
  .video__preview-icon:active {
    transform: scale(80%);
  }
  .video__info {
    grid-row: 1 / 5;
    padding: 171px 36px 0 79px;
    border: 1px solid #ececec;
  }
  .video__info:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }
  .video__info:active {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  }
  .video__description {
    padding-bottom: 51px;
    margin: 0;
  }
  .video__button {
    width: 230px;
    margin: 0;
  }
}
