  padding: 28px 30px 46px 30px;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: auto;
  background: #ffffff;
  box-sizing: border-box;
  border-top: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
